import { ComponentProps } from 'preact';
import { Icon } from './Icon';
import { RootTheme } from '../UNSAFE_Environment';
import { useTheme } from '../hooks/UNSAFE_useTheme';

type Props = Omit<ComponentProps<typeof Icon>, 'viewBox'>;

type ThemeIcons = Record<RootTheme['name'], typeof Icon>;

// Returns a component that renders one of the given icon components based on theme
export const withThemeIcon = (themeIcons: ThemeIcons) => {
  const { name } = useTheme();
  return (props: Props) => {
    const Component = themeIcons[name];
    return <Component {...props} />;
  };
};
